
import Vue from 'vue'
import Vuex from 'vuex'
import {request}  from '../utils/util.js'
Vue.use(Vuex)
const store = new Vuex.Store({








				state: {
					hasLogin: false, // ,false
					locationInformation: '', //
					currentLatitude: '31.359028', // 
					currentLongitude: '117.184506', // 
					whitelistInfo: [], //

				},
				mutations: {
					login(state, provider) {
						state.hasLogin = true;
						state.loginProvider = provider;
					},
					logout(state) {
						state.hasLogin = false
						state.openid = null
					},
					setOpenid(state, openid) {
						state.openid = openid
					},
					setTestTrue(state) {
						state.testvuex = true
					},
					setTestFalse(state) {
						state.testvuex = false
					},
					setColorIndex(state, index) {
						state.colorIndex = index
					},
					setMatchLeftWindow(state, matchLeftWindow) {
						state.noMatchLeftWindow = !matchLeftWindow
					},
					setActive(state, tabPage) {
						state.active = tabPage
					},
					setLeftWinActive(state, leftWinActive) {
						state.leftWinActive = leftWinActive
					},
					setActiveOpen(state, activeOpen) {
						state.activeOpen = activeOpen
					},
					setMenu(state, menu) {
						state.menu = menu
					},
					setUniverifyLogin(state, payload) {
						typeof payload !== 'boolean' ? payload = !!payload : '';
						state.isUniverifyLogin = payload;
					},
					setUniverifyErrorMsg(state, payload = '') {
						state.univerifyErrorMsg = payload
					},
					setWhitelistInfo(state, data) {
						state.whitelistInfo = data
					},
					getLocation(state, data) {
						console.log('' + data)
						if (data['address_component']) {
							state.locationInformation = data['address_component'][
								'city'] //actionsstate
							state.currentLatitude = data['location']['lat']
							state.currentLongitude = data['location']['lng']
						} else {
							state.locationInformation = ''
						}
					},
				},
				getters: {
					currentColor(state) {
						return state.colorList[state.colorIndex]
					}
				},

				actions: {
					// lazy loading openid
					getUserOpenId: async function({
						commit,
						state
					}) {
						return await new Promise((resolve, reject) => {
							if (state.openid) {
								resolve(state.openid)
							} else {
								uni.login({
									success: (data) => {
										commit('login')
										setTimeout(function() { // openid
											const openid = '123456789'
											console.log(
												'uni.request mock openid[' +
												openid + ']');
											commit('setOpenid', openid)
											resolve(openid)
										}, 1000)
									},
									fail: (err) => {
										console.log('uni.login ',
											err)
										reject(err)
									}
								})
							}
						})
					},
					getPhoneNumber: function({
						commit
					}, univerifyInfo) {
						return new Promise((resolve, reject) => {
							uni.request({
								url: 'https://97fca9f2-41f6-449f-a35e-3f135d4c3875.bspapp.com/http/univerify-login',
								method: 'POST',
								data: univerifyInfo,
								success: (res) => {
									const data = res.data
									if (data.success) {
										resolve(data.phoneNumber)
									} else {
										reject(res)
									}

								},
								fail: (err) => {
									reject(res)
								}
							})
						})
					},
					getWhitelistInfo: async function(context, data) {
						//
						return await  request(getWhitelist,
							data, 'POST').then(res => {
								context.commit("setWhitelistInfo", res.data.data)		
						})				
					},
					// 
					getLocation(context, data) {
						return new Promise((resolve, reject) => {
							let _this = data;
							uni.getLocation({
								type: 'gcj02', //uni.openLocation
								success: function(res) {
									console.log('' + res)
									const latitude = res.latitude;
									const longitude = res.longitude;
									let options = { //
										latitude: res.latitude,
										longitude: res.longitude,
									}
									uni.setStorageSync('currentLatitude', latitude)
									uni.setStorageSync('currentLongitude', longitude)
									_this.$map.reverseGeocoder({
										//   
										//   location:{
										//       latitude:'31.359028',
										//       longitude:':117.184506',
										//   }
										// */
										location: options || {
											latitude: '31.359028',
											longitude: '117.184506',
										}, //,,string 
										success: function(res) { //
											console.log('' + res)
											context.commit("getLocation", res
												.result)

											resolve(res)
										},
										fail: function(error) {
											console.error(error);
										},
										complete: function(res) {

										}
									})


								},
								fail(error) {
									console.log(error)
									let getLocationAuth = function() {
										uni.getSetting({
											success(res) {
												if (res.authSetting[
														'scope.userLocation']) {
													return;
												} else {
													uni.showModal({
														content: '',
														confirmText: "",
														cancelText: '',
														success: (res) => {
															if (res
																.confirm
																) {
																uni.openSetting({
																	success: (
																		res
																		) => {
																		if (res
																			.authSetting[
																				'scope.userLocation'
																				]
																			) {
																			uni.showToast({
																				title: '',
																				icon: 'none',
																				success: function() {
																					uni.getLocation({
																						type: 'gcj02', //uni.openLocation
																						isHighAccuracy: true,
																						success: function(
																							res
																							) {
																							const
																								latitude =
																								res
																								.latitude;
																							const
																								longitude =
																								res
																								.longitude;
																							let options = { //
																								latitude: res
																									.latitude,
																								longitude: res
																									.longitude,
																							}
																							_this
																								.$map
																								.reverseGeocoder({
																									//   
																									//   location:{
																									//       latitude:'31.359028',
																									//       longitude:':117.184506',
																									//   }
																									// */
																									location: options ||
																									{
																										latitude: '31.359028',
																										longitude: '117.184506',
																									}, //,,string 
																									success: function(
																										res
																										) { //
																										context
																											.commit(
																												"getLocation",
																												res
																												.result
																												)

																										resolve
																											(
																												res)
																									},
																									fail: function(
																										error
																										) {
																										console
																											.error(
																												error
																												);
																									},
																									complete: function(
																										res
																										) {

																									}
																								})


																						},
																					})
																				}
																			})
																		} else {
																			getLocationAuth
																				()
																		}
																	},
																	fail: (
																		err) => {
																		console
																			.log(
																				err
																				)
																	}
																})
															} else {
																getLocationAuth
																	()
															}
														}
													})
												}
											},
											fail(res) {
												console.error(error);
											}
										})
									}

									getLocationAuth()
								}
							});
						})
					},

					//  
					enablePullDownRefresh(context, payload) {
						payload.this.$utils.request(payload.this.url, payload.data, 'GET').then((res) => {
							payload.this.list = res.data.data


						}).catch((error) => {
							console.log(error)
						})
					},

					//  
					pullLoading(context, payload) {
						payload.this.$utils.request(payload.this.url, payload.data, 'GET').then((res) => {
							res.data.data.map(item => {
								payload.this.list.push(item)
							})
						}).catch((error) => {
							console.log(error)
						})
					},
					// 
					getSubscribe() {
						uni.getSetting({
							withSubscriptions: true,
							success(res) {
								uni.setStorageSync('isSubscribe', true);
								if (!res.subscriptionsSetting.itemSettings) {
									uni.setStorageSync('isSubscribe', false);
								}
								for (let i in res.subscriptionsSetting.itemSettings) {
									if (res.subscriptionsSetting.itemSettings[
											i] === 'reject') {
										uni.setStorageSync('isSubscribe', false);
									}
								}

							}
						})
					}
				}
			})

			export default store
